import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import Logo from "../../assets/Icons/ARCA_Logo_white.png";
import "./Login.css";
import { Container, Button, Grid, TextField } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  container: {
    padding: theme.spacing(5),
  },
}));

const Login = ({}) => {
  const classes = useStyles();
  let history = useHistory();
  const [validation, setValidation] = useState(false)
  const [loginForm, setLoginForm] = useState({
    username: "",
    password: "",
  });
  const handleChange = (e) => {
    const { name, value } = e.target;
    setLoginForm({ ...loginForm, [name]: value });
  };

  const submitLoginForm = (e) => {
    if (
      loginForm.username === "arcauser" &&
      loginForm.password === "password" &&
      loginForm.password.length>=8
    ) {
      e.preventDefault();
      history.push("/dashboard");
    }else{
      setValidation(true)
    }
  };

  useEffect(()=>{
    if(loginForm.username === "arcauser" && loginForm.password === "password"){
      setValidation(false)
    }
  },[loginForm])

  return (
    <div className="df-login-container">
      <div className="login-container">
        <Container className={classes.container} maxWidth="xs">
          {/* <form onSubmit={submitLoginForm} style={{ marginTop: "25px" }}> */}
            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
              spacing={0}
              mt-3
            >
              <img src={Logo} style={{ width: "130px", height: "30px" }} />
            </Grid>
            <div className="arca-login-form">
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Username"
                        name="username"
                        size="small"
                        variant="outlined"
                        value={loginForm.username}
                        onChange={(e) => handleChange(e)}
                        InputLabelProps={{ style: { fontSize: 14 } }}
                        autoComplete="off"
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Password"
                        name="password"
                        size="small"
                        type="password"
                        variant="outlined"
                        InputLabelProps={{ style: { fontSize: 14 } }}
                        value={loginForm.password}
                        onChange={(e) => handleChange(e)}
                        autoComplete="off"
                      />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12}>
                  <Button
                    color="primary"
                    fullWidth
                    type="submit"
                    variant="contained"
                    onClick={submitLoginForm}
                    style={{marginBottom:10}}
                  >
                    <span style={{ color: "white", fontWeight: "bolder" }}>
                      Login
                    </span>
                  </Button>
                  {validation ? <span style={{color:'red', marginTop:5}}>Invalid username or password</span>:null}
                </Grid>
              </Grid>
            </div>
          {/* </form> */}
        </Container>
        <p className="jaitra"> Jaitra Inc</p>
      </div>
    </div>
  );
};

export default Login;
