import { useEffect, useState } from "react";
import axios from "axios";
import { Switch, Grid, Typography, Checkbox, Button, CircularProgress,Snackbar } from "@material-ui/core";
import load_balance from "../../assets/icons_v2/load_balance.svg";
import "./LoadBalancer.css";
import Navbar from "../ARCASideNav/Navbar";
import { makeStyles } from "@material-ui/core/styles";
import { ThreeSixty } from "@material-ui/icons";
// import TextField from '@material-ui/core/TextField';
const loadBalancing = [
  {
    id: '1',
    interfaceName: 'WAN1',
    ISPName: 'Airtel_privatenet',
    weight: '50',
  },
  {
    id: '2',
    interfaceName: 'WAN2',
    ISPName: 'Jio_privatenet',
    weight: '50',
  },
  {
    id: '3',
    interfaceName: 'WAN3',
    ISPName: 'ACT',
    weight: '100',
  }
]

const LoadBalancer = () => {
  const [toggleLoad, setToggleLoad] = useState(true);
  const [toggleBalancer, setToggleBalancer] = useState(false);
  const [loadData,setLoadData] = useState();
  const [checkboxVal,setCheckboxVal] = useState(true)
  const [loadBalenceData, setLoadBalenceData] = useState({})
  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);

  const handleClose = () => {
    setOpen(false);
  };

  const handleClose2 = () => {
    setOpen2(false);
  };

  const toggleBalancerChange = (event) => {
    setToggleBalancer(event.target.checked)
    if(event.target.checked===true){
      const res = axios.get('/lbon')
    }
    else if (event.target.checked===false){
      const res = axios.get('/lboff')
    }
  }
  const toggler = (event) => {
    setToggleLoad(event.target.checked);
    if (event.target.checked === true) {
      const res = axios
        .get("/loadbalancingon")
        .catch((err) => console.log(err));
    } else if (event.target.checked === false) {
      const resp = axios
        .get("/loadbalancingoff")
        .catch((err) => console.log(err));
    }
  };

  useEffect(() => {
    async function getStatus() {
      try {
        const res = await axios.get("/loadbalancingstatus");
        setToggleLoad(res.data.result);
        const loaddata = await axios.get('/getloadbalance')
        // console.log(loaddata.data)
        if(loaddata.data){
          setLoadData(loaddata.data);
          const initialKeys = [ 'ispName','interfaceName', 'checked', 'weight']
          const loadBalencerDataArray = []
          loaddata.data.forEach(element => {
            const itemData = {}
            itemData[initialKeys[0]] = element[0]
            itemData[initialKeys[1]] = element[1]
            itemData[initialKeys[2]] = element[2]
            itemData[initialKeys[3]] = element[3]
            loadBalencerDataArray.push(itemData)
          });
            setLoadBalenceData({balencerData:loadBalencerDataArray})
        }else{
          setLoadData([])
        }
      const toggle = await axios.get('/lbstatus');
      setToggleBalancer(toggle.data.result);
      setCheckboxVal();
      } catch (error) {
        console.log(error);
      }
    }
    getStatus();
    
  }, []);
  const handleChangeChecked = (e, item)=>{
    let updateList = loadBalenceData.balencerData.map(obj =>{
      if(obj.interfaceName === item.interfaceName){
        return Object.assign({}, obj, {
          checked: e.target.checked
        })
      }
      return obj;
    })
    setLoadBalenceData({balencerData:updateList})
    
    
  }

  useEffect(()=>{
    const res = axios.post('/setlbinterface',loadBalenceData.balencerData);
  },[loadBalenceData.balencerData])
  const handleChangeWeight = (e, item) => {
    let updatedList1 = loadBalenceData.balencerData.map(obj => {
      if (obj.interfaceName === item.interfaceName) {
        return Object.assign({}, obj, {
          weight: e.target.value
        });
      }
      return obj;
    });
    setLoadBalenceData({ balencerData: updatedList1 })
  }
  const handleSave = () => {
    //console.log(loadBalenceData.balencerData)

    async function saveCheck(){
      const res = await axios.post('/setloadbalance',loadBalenceData.balencerData);
            if(res.data.result==="Success"){
        setOpen(true);
      }
      else if(res.data.result==="failed"){
        setOpen2(true);
      }
    }
    saveCheck();
    
    
  }

  return (
    <>
      <Navbar />
      
      <div className="load-container">
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <div style={{ marginLeft: 10 }}>
              <img src={load_balance} className="load-icon" />
              <p className="lb-heading">Load Balancer</p>
              <p className="lbsub-heading">
                Information about Load Balancer
              </p>
            </div>
          </Grid>
          <Grid xs={3}>
            <div style={{ marginTop: "15px" }}>
              <Typography component="div">
                <Grid
                  component="label"
                  container
                  alignItems="center"
                  spacing={1}
                >
                  <Grid item>Off</Grid>
                  <Grid item>
                    <Switch
                      checked={toggleLoad}
                      onChange={toggler}
                      color="primary"
                      name="speedboost"
                      inputProps={{ "aria-label": "primary checkbox" }}
                    />
                  </Grid>
                  <Grid item>On</Grid>
                </Grid>
              </Typography>
            </div>
          </Grid>
        </Grid>
        <hr style={{ marginLeft: 15, marginRight: 17, color:"#E0E5E5"}} />
        {loadBalenceData.balencerData?
        <>
        <div style={{ margin: '20px' }}>
          <Typography component="div">
            <Grid component="label" container alignItems="center" spacing={2}>
              <Grid item>
                <Typography className="toggle-head" style={!toggleLoad ? { color: '#dddddd' } : {}}>
                  Static Load Balancer
                </Typography>
                <Typography className="toggle-subhead">
                  Connect to available networks
                </Typography>
              </Grid>
              <Grid item>
                <Switch
                  checked={toggleBalancer}
                  onChange={toggleBalancerChange}
                  color="primary"
                  name="speedboost"
                  inputProps={{ "aria-label": "Switch A" }}
                  disabled={!toggleLoad}
                />
              </Grid>
              <Grid item>
                <Typography className="toggle-head" style={!toggleLoad ? { color: '#dddddd' } : {}}>
                  Dynamic Load Balancer
                </Typography>
                <Typography className="toggle-subhead">
                  Connect to available networks
                </Typography>
              </Grid>
            </Grid>
          </Typography>
        </div>
        <div style={{ margin: '20px' }}>
          {!toggleBalancer ? (<table>
            <thead>
              <tr style={!toggleLoad ? { color: '#f3efef' } : {}}>
                <th style={{ textAlign: 'center' }}>Interface</th>
                <th style={{ textAlign: 'start' }}>ISP Name</th>
                <th style={{ textAlign: 'start' }}>Weight(%)</th>
              </tr>
            </thead>
            <tbody>
              {loadBalenceData.balencerData.map((item,index) => {
                // console.log(item)
                return (
                  <tr key={item.interfaceName} style={!toggleLoad ? { color: '#dddddd' } : {}}>
                    <td style={!item.checked ? { textAlign: 'start', color: '#dddddd' }:{}}> 
                    <Checkbox
                      color="primary"
                      // checked={item[2]}
                      checked = {item.checked}
                      onChange={(e)=>{handleChangeChecked(e, item)}}
                      inputProps={{ 'aria-label': 'secondry checkbox' }}
                      disabled={!toggleLoad}
                      style={!item.checked ? { textAlign: 'start', color: '#dddddd' }:{}}
                    /> 
                    {item.interfaceName}</td>
                    <td style={!item.checked ? { textAlign: 'start', color: '#dddddd' }:{}}>{item.ispName}</td>
                    <td style={{ textAlign: 'start' }}>
                      <input type="text" name="weight"
                        // onChange={handleChangeWeight}
                        onChange={(e)=>{handleChangeWeight(e, item)}}
                        style={!item.checked || !toggleLoad ? { width: '70px', height: '20px', color:'#dddddd', borderColor:'#dddddd' }:{width: '70px', height: '20px'}}
                        value={item.weight}
                        // hidden={!toggleLoad}
                      />
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>) : (
            <table>
              <thead>
                <tr style={!toggleLoad ? { color: '#f3efef' } : {}}>
                  <th style={{ textAlign: 'center' }}>Interface</th>
                  <th style={{ textAlign: 'start' }}>ISP Name</th>
                  <th style={{ textAlign: 'start' }}>Weight(%)</th>
                </tr>
              </thead>
              <tbody>
                {loadBalenceData.balencerData?.map((item,index) => {
                  return (
                    <tr key={item.index} style={!toggleLoad ? { color: '#dddddd' } : {}}>
                      <td style={!item.checked ? { textAlign: 'start', color: '#dddddd' }:{}}>
                        <Checkbox
                        color="primary"
                        checked = {item.checked}
                        onChange={(e)=>{handleChangeChecked(e, item)}}
                        inputProps={{ 'aria-label': 'secondry checkbox' }}
                        disabled={!toggleLoad}
                        style={!item.checked ? { textAlign: 'start', color: '#dddddd' }:{}}
                      /> 
                      {item.interfaceName}</td>
                      <td style={!item.checked ? { textAlign: 'start', color: '#dddddd' }:{}}>{item.ispName}</td>
                      <td style={!item.checked ? { textAlign: 'start', color: '#dddddd' }:{}}>{item.weight}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          )}
        </div>
      
          <Button variant="contained" color="primary" size="small" style={{ marginLeft: 13 }} disabled={!toggleLoad} onClick={handleSave}>
            <span
              style={{
               
                color: "white",
              }}
            >
              Save
            </span>
          </Button>
          {open ? (
              <Snackbar
                open={open}
                autoHideDuration={3000}
                onClose={handleClose}
                action={
                  <>
                    <div style={{ display: "flex", placeContent: "center" }}>
                      Saved Succesfully
                    </div>
                  </>
                }
              ></Snackbar>
            ) : (
              ""
            )}
            {open2 ? (
              <Snackbar
                open={open2}
                autoHideDuration={3000}
                onClose={handleClose2}
                action={
                  <>
                    <div style={{ display: "flex", placeContent: "center" }}>
                      Total Sum should be 100!
                    </div>
                  </>
                }
              ></Snackbar>
            ) : (
              ""
            )}
        
        </>:<div className="load-loader">
<CircularProgress/>
</div>}
      </div>
    </>
  );
};

export default LoadBalancer;
