import './App.css';
import './index.css';
import { useState } from 'react';
import {BrowserRouter,Route,Switch, Redirect} from "react-router-dom";
import DashBoard from "./components/dashboard/DashBoard";
import SpeedTest from "./components/speedtest/SpeedTest";
import WirelessLAN from "./components/wirelesslan/WirelessLAN";
import ParentalControl from "./components/parentalcontrol/ParentalControl";
import Payments from "./components/payments/Payments";
import VPNSettings from "./components/vpnsettings/VPNSettings";
import SpeedBoost from "./components/speedboost/SpeedBoost";
import LoadBalancer from "./components/loadbalancer/LoadBalancer";
import Navbar from "./components/ARCASideNav/Navbar";
import Login from "./components/Login/Login";
import LAN from "./components/lan/LAN";
import WAN from "./components/wan/WAN";
import NetWorkStorage from "./components/networkstorage/NetworkStorage";


function App() {


  return (
  <>
    <BrowserRouter>
    <Switch>
    <Route exact path="/" component={Login}/>
      <Route path="/dashboard" component={DashBoard}/>
      <Route path="/speedtest" component={SpeedTest}/>
      <Route path="/wirelesssettings" component={WirelessLAN}/>
      <Route path="/vpnsettings" component={VPNSettings}/>
      <Route path="/speedboost" component={SpeedBoost}/>
      <Route path="/loadbalancer" component={LoadBalancer}/>
      <Route path="/parentalcontrol" component={ParentalControl}/>
      <Route path="/payments" component={Payments}/>
      <Route path="/lan" component={LAN}/>
      <Route path="/wan" component={WAN}/>
      <Route path="/networkstorage" component={NetWorkStorage}/>


    </Switch>
    </BrowserRouter>
    </>
  );
}

export default App;
